@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,300,500,600,700,800);
#ufv-map-root {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Open Sans", sans-serif;
  /*.building .initial { opacity: .7; }*/
}
#ufv-map-root *, #ufv-map-root *:after, #ufv-map-root *:before {
  box-sizing: border-box;
}
#ufv-map-root .stage {
  position: relative;
  overflow: hidden;
}
@media screen and (min-width: 750px) {
  #ufv-map-root .stage {
    display: flex;
  }
  #ufv-map-root .stage > div {
    width: 50%;
    position: relative;
  }
}
#ufv-map-root .player {
  background: #AEC6AA;
}
#ufv-map-root .instructions {
  display: none;
}
@media screen and (min-width: 750px) {
  #ufv-map-root .instructions {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  #ufv-map-root .instructions h1 {
    background: #7cb232;
    padding: 20px 20px;
    color: white;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 10px;
    font-size: 18px;
    text-align: center;
    font-weight: normal;
  }
}
#ufv-map-root .modal {
  pointer-events: none;
  display: flex;
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease-out;
  z-index: 1;
}
#ufv-map-root .modal .backdrop {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.5);
}
#ufv-map-root .modal .video-wrap {
  overflow: hidden;
  position: relative;
  height: 0;
  width: 90%;
  padding: 0;
  padding-bottom: calc(90% * (4/3));
  background: black;
  border-radius: 10px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
}
#ufv-map-root .modal .video-wrap:after {
  position: absolute;
  content: "";
  height: 20%;
  width: 100%;
  bottom: 0;
  left: 0;
  background: linear-gradient(to top, black 20%, rgba(0, 0, 0, 0) 100%);
}
#ufv-map-root .modal .controls {
  position: absolute;
  bottom: 0px;
  width: 100%;
  z-index: 1;
}
#ufv-map-root .modal .controls .icon {
  width: 28px;
  height: 28px;
  text-indent: -9999px;
  background-repeat: no-repeat;
  background-position: center;
  padding: 15px;
  box-sizing: content-box;
  cursor: pointer;
}
#ufv-map-root .modal .controls .icon.back {
  background-image: url(./images/back.svg);
}
#ufv-map-root .modal .controls .icon.sound {
  background-image: url(./images/sound-unmute.svg);
}
#ufv-map-root .modal .controls .icon.sound.active {
  background-image: url(./images/sound-mute.svg);
}
#ufv-map-root .modal .controls .icon.emoji-homg {
  background-image: url(./images/emoji-homg.svg);
}
#ufv-map-root .modal .controls .icon.emoji-stars {
  background-image: url(./images/emoji-stars.svg);
}
#ufv-map-root .modal .controls .icon.emoji-smile {
  background-image: url(./images/emoji-smile.svg);
}
#ufv-map-root .modal .controls .top {
  display: flex;
  align-items: flex-end;
}
#ufv-map-root .modal .controls .caption-wrap {
  flex: 1;
  padding-right: 58px;
  text-align: center;
}
#ufv-map-root .modal .controls .captions {
  display: inline-block;
  padding: 5px 12px;
  color: white;
  background: rgba(0, 0, 0, 0.8);
  margin: 0 25px 15px;
  border-radius: 10px;
  text-align: center;
}
#ufv-map-root .modal .controls .meta-reaction {
  padding: 0px 15px 10px;
  display: flex;
  align-items: center;
}
#ufv-map-root .modal .controls .meta-reaction .icon {
  padding: 5px;
}
@media screen and (min-width: 375px) {
  #ufv-map-root .modal .controls .meta-reaction .icon {
    padding: 10px;
  }
}
@keyframes spin {
  0% {
    transform: rotate(0) scale(1);
  }
  50% {
    transform: rotate(360deg) scale(1.25);
  }
  100% {
    transform: rotate(720deg) scale(1);
  }
}
#ufv-map-root .modal .controls .meta-reaction .icon.emote {
  animation: spin 500ms;
}
#ufv-map-root .modal .controls .meta-reaction .name {
  flex: 1;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  color: white;
  font-weight: bold;
  font-size: 18px;
}
#ufv-map-root .modal .controls .progress {
  height: 8px;
  width: 100%;
  transition: transform 250ms linear;
  transform-origin: bottom left;
  transform: scaleX(0.0001);
  background: #427D03;
}
#ufv-map-root .modal video {
  border-radius: 10px;
  display: block;
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  transition: transform 0.3s ease-out;
}
@media screen and (min-width: 750px) {
  #ufv-map-root .modal .video-wrap {
    width: 100%;
    padding-bottom: calc(100% * (4/3));
    box-shadow: none;
  }
  #ufv-map-root .modal .video-wrap, #ufv-map-root .modal .name, #ufv-map-root .modal video {
    border-radius: 0;
  }
}
#ufv-map-root .modal.active {
  pointer-events: initial;
  opacity: 1;
}
#ufv-map-root .building {
  cursor: pointer;
}
#ufv-map-root .building .hover, #ufv-map-root .building.visited .initial,
#ufv-map-root .building .active {
  display: none;
}
#ufv-map-root .building:hover .hover, #ufv-map-root .building.visited .active {
  display: block;
}
#ufv-map-root .building:hover .initial {
  opacity: 1;
}
#ufv-map-root .locations {
  position: relative;
}
#ufv-map-root .locations .toggle {
  background: url(./images/toggle-map.svg);
  background-size: contain;
  width: 98px;
  height: 62px;
  z-index: 1;
  position: absolute;
  bottom: 2px;
  right: 2px;
  text-indent: -9999px;
  cursor: pointer;
}
#ufv-map-root .locations .toggle:after {
  content: "";
  position: absolute;
  top: -5px;
  bottom: -10px;
  left: -5px;
  right: -10px;
}
#ufv-map-root .locations .toggle.active {
  background-image: url(./images/toggle-list.svg);
}
#ufv-map-root .locations .map {
  position: relative;
  height: 0;
  width: 100%;
  padding: 0;
  padding-bottom: calc(100% * (4/3));
}
#ufv-map-root .locations .map svg {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}
#ufv-map-root .locations .list {
  position: absolute;
  background: rgba(255, 255, 255, 0.92);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: auto;
  display: none;
}
#ufv-map-root .locations .list.active {
  display: block;
}
#ufv-map-root .locations .location {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 3px 5px;
}
@media screen and (min-width: 750px) {
  #ufv-map-root .locations .location {
    padding: 5px 10px;
  }
}
#ufv-map-root .locations .location .icon {
  object-fit: cover;
  width: 40px;
  height: 40px;
  margin-right: 10px;
  border-radius: 100%;
  border: 2px solid #AEC6AA;
}
#ufv-map-root .locations .location h1 {
  margin: 0;
  flex: 1;
  font-size: 18px;
  font-weight: normal;
}
@media screen and (min-width: 750px) {
  #ufv-map-root .locations .location h1 {
    font-size: 22px;
  }
}
#ufv-map-root .locations .location.visited {
  color: #888;
}
#ufv-map-root .locations .location:hover {
  color: #E45300;
}
#ufv-map-root .locations .location.active {
  color: #E45300;
  background: white;
}
#ufv-map-root .locations .location.active .icon {
  border-color: #E45300;
}